.App {
  text-align: center;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}


.App-header {
  background-color: #634646;
  min-height: 18vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(228, 121, 17);
}

.App-link {
  color: #61dafb;
}

.Containers{
  display:flex;
  flex-direction:row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.Conteiner{
  display:flex;
  flex-direction:row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.App-Productos{
  width:40%;
  margin-top: 50px;
  margin-left: 50px;
  table-layout: auto;

}

.Listado{
  width:40%;
  margin-top: 50px;
  margin-left: 50px;
  table-layout: auto;
}

.Containers img{
  height: auto; 
  width: auto; 
  max-width: 50px; 
  max-height: 50px;
}

.Conteiner img{
  height: auto; 
  width: auto; 
  max-width: 40px; 
  max-height: 40px;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
